:root {
    --brand-primary: #245eac;
    --primary: #245eac;
    --brand-secondary: #e6252f;
    --secondary: #0f0f0f;

    --black: #0f0f0f;

    --gray-darkest: hsl(0, 0%, 15%);
    --gray-darker: hsl(0, 0%, 35%);
    --gray-dark: hsl(0, 0%, 50%);
    --gray: hsl(0, 0%, 70%);
    --gray-light: hsl(0, 0%, 80%);
    --gray-lighter: hsl(0, 0%, 90%);
    --gray-lightest: hsl(0, 0%, 97%);

    --quick-obs: #4caf50;
    --avalanche-obs: #03a9f4;
    --snowpack-obs: #3f51b5;
    --weather-obs: #ffc107;
    --incident-obs: #f44336;

    --spaw: #e6252f;
    --spaw-hover: rgb(217, 37, 46);
    --spaw-hover: rgb(217, 37, 46);

    --success: #4caf50;
    --info: #03a9f4;
    --warning: #ffc107;
    --danger: #e6252f;

    --error: #e6252f;
}

:root {
    --border-color: hsl(0, 0%, 97%);
    --border-color: var(--gray-lightest);
}

._3ryZ {
    display: flex;
    flex-direction: column;

    width: 100%;
    /*margin: 0 0 2em 0;*/
    min-height: 3em;
    line-height: 3em;
    border-bottom: 2px solid hsl(0, 0%, 97%);
    border-bottom: 2px solid var(--border-color);
    text-transform: uppercase;
    font-weight: 700;
    color: hsl(0, 0%, 50%);
    color: var(--gray-dark)

}

@media (min-width: 601px) {

._3ryZ {
        flex-direction: row

}
    }

.zoGt {
    flex: 1;
    padding: 0 0.5em;
    min-width: 125px;
    border-left: 2px solid hsl(0, 0%, 97%);
    border-left: 2px solid var(--border-color)
}

.zoGt:first-child {
        border-left: none;
    }

.zoGt:last-child {
        flex: auto;
    }
