    --component-active-bg: #245eac;

    --component-active-bg: var(--primary);
    --component-active-bg-hover: hsl(214.41176470588235, 65.3846153846%, 75%);
    --component-active-bg-hover: hsl(214.41176470588235, 65.3846153846%, 75%);
