    --component-active-bg-active: hsl(214.41176470588235, 65.3846153846%, 60%);

    --component-primary-bg: #245eac;

    --component-primary-bg: var(--primary);
    --component-primary-bg-hover: hsl(214.41176470588235, 65.3846153846%, 75%);
    --component-primary-bg-hover: hsl(214.41176470588235, 65.3846153846%, 75%);
    --component-primary-bg-active: hsl(214.41176470588235, 65.3846153846%, 60%);
    --component-primary-bg-active: hsl(214.41176470588235, 65.3846153846%, 60%);

    --component-secondary-bg: #0f0f0f;

    --component-secondary-bg: var(--secondary);
    --component-secondary-bg-hover: hsl(0, 0%, 75%);
    --component-secondary-bg-hover: hsl(0, 0%, 75%);
    --component-secondary-bg-active: hsl(0, 0%, 60%);
    --component-secondary-bg-active: hsl(0, 0%, 60%);

    --component-tertiary-bg: #e6252f;

    --component-tertiary-bg: var(--brand-secondary);
    --component-tertiary-bg-hover: hsl(356.8911917098445, 79.4238683128%, 75%);
    --component-tertiary-bg-hover: hsl(356.8911917098445, 79.4238683128%, 75%);
    --component-tertiary-bg-active: hsl(356.8911917098445, 79.4238683128%, 60%);
    --component-tertiary-bg-active: hsl(356.8911917098445, 79.4238683128%, 60%);
