:root {
    --brand-primary: #245eac;
    --primary: #245eac;
    --brand-secondary: #e6252f;
    --secondary: #0f0f0f;

    --black: #0f0f0f;

    --gray-darkest: hsl(0, 0%, 15%);
    --gray-darker: hsl(0, 0%, 35%);
    --gray-dark: hsl(0, 0%, 50%);
    --gray: hsl(0, 0%, 70%);
    --gray-light: hsl(0, 0%, 80%);
    --gray-lighter: hsl(0, 0%, 90%);
    --gray-lightest: hsl(0, 0%, 97%);

    --quick-obs: #4caf50;
    --avalanche-obs: #03a9f4;
    --snowpack-obs: #3f51b5;
    --weather-obs: #ffc107;
    --incident-obs: #f44336;

    --spaw: #e6252f;
    --spaw-hover: rgb(217, 37, 46);
    --spaw-hover: rgb(217, 37, 46);

    --success: #4caf50;
    --info: #03a9f4;
    --warning: #ffc107;
    --danger: #e6252f;

    --error: #e6252f;
}

._3ZL5 {
    /* If you change the display: look at the gear page: https://www.avalanche.ca/gear. It was set to display: inline-block before.  */
    display: block;
    color: hsl(0, 0%, 35%);
    color: var(--gray-darker);
    margin: 0;
    padding: 0;
}

._2ZuQ {
    width: 100%;
    padding: 1em;
    box-sizing: border-box;
    background-color: hsl(0, 0%, 90%);
    background-color: var(--gray-lighter)
}

._2ZuQ > p {
        margin: 0;
    }

.image-gallery ._2ZuQ > p {
        /* FIXME: So weird the styling here. line-height!!! */
