:root {
    --brand-primary: #245eac;
    --primary: #245eac;
    --brand-secondary: #e6252f;
    --secondary: #0f0f0f;

    --black: #0f0f0f;

    --gray-darkest: hsl(0, 0%, 15%);
    --gray-darker: hsl(0, 0%, 35%);
    --gray-dark: hsl(0, 0%, 50%);
    --gray: hsl(0, 0%, 70%);
    --gray-light: hsl(0, 0%, 80%);
    --gray-lighter: hsl(0, 0%, 90%);
    --gray-lightest: hsl(0, 0%, 97%);

    --quick-obs: #4caf50;
    --avalanche-obs: #03a9f4;
    --snowpack-obs: #3f51b5;
    --weather-obs: #ffc107;
    --incident-obs: #f44336;

    --spaw: #e6252f;
    --spaw-hover: rgb(217, 37, 46);
    --spaw-hover: rgb(217, 37, 46);

    --success: #4caf50;
    --info: #03a9f4;
    --warning: #ffc107;
    --danger: #e6252f;

    --error: #e6252f;
}

._1xG_ {
    display: flex;
    flex-direction: row;
    align-items: center;

    text-align: center;
    margin: 0
}

@media (min-width: 501px) {

._1xG_ {
        flex-direction: column
}
    }

._2YmR {
    width: 6em;
    color: hsl(0, 0%, 80%);
    color: var(--gray-light);
    font-weight: 700;
    font-size: 0.75em;
    text-transform: uppercase;
    margin-bottom: 0.5em
}

@media (min-width: 501px) {

._2YmR {
        width: auto;
        margin-right: auto;
        margin-left: auto
}
    }

.ZpD1 {
    flex: 1;
    margin: 0 auto
}

.ZpD1 img {
        max-width: 150px;
        display: block;
        margin: 0 auto;
    }
