:root {
    --brand-primary: #245eac;
    --primary: #245eac;
    --brand-secondary: #e6252f;
    --secondary: #0f0f0f;

    --black: #0f0f0f;

    --gray-darkest: hsl(0, 0%, 15%);
    --gray-darker: hsl(0, 0%, 35%);
    --gray-dark: hsl(0, 0%, 50%);
    --gray: hsl(0, 0%, 70%);
    --gray-light: hsl(0, 0%, 80%);
    --gray-lighter: hsl(0, 0%, 90%);
    --gray-lightest: hsl(0, 0%, 97%);

    --quick-obs: #4caf50;
    --avalanche-obs: #03a9f4;
    --snowpack-obs: #3f51b5;
    --weather-obs: #ffc107;
    --incident-obs: #f44336;

    --spaw: #e6252f;
    --spaw-hover: rgb(217, 37, 46);
    --spaw-hover: rgb(217, 37, 46);

    --success: #4caf50;
    --info: #03a9f4;
    --warning: #ffc107;
    --danger: #e6252f;

    --error: #e6252f;
}

.Hz8J {
    display: inline-block;
    padding: 0.35em;
    font-weight: 700;
    color: white;
    text-align: center;
    white-space: pre-wrap;
    background-color: #e6252f;
    background-color: var(--spaw)
}

.Hz8J[href]:hover {
            background-color: rgb(217, 37, 46);
            background-color: var(--spaw-hover);
        }

.Hz8J[href]:after {
            content: '\A0\203A';
        }
