    --error: #e6252f;
}

.jXD8 {
    position: relative;
    background-color: white
}

.jXD8 svg {
        height: 40px;
        padding: 0 6px;
    }

.jXD8 > svg {
        position: absolute;
        pointer-events: none
    }

.jXD8 > svg:first-child {
            left: 0;
        }

.jXD8 > svg:last-child {
            right: 0;
        }

.jXD8 > button {
        padding: 0;
    }

.jXD8 path:not([fill='none']) {
        fill: #245eac;
        fill: var(--primary);
    }

._3I5h {
    position: absolute;
    top: 0;
    right: 0;
}

._30Z9 {
    padding-left: 32px;
    padding-right: 32px;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
}
