:root {
    --brand-primary: #245eac;
    --primary: #245eac;
    --brand-secondary: #e6252f;
    --secondary: #0f0f0f;

    --black: #0f0f0f;

    --gray-darkest: hsl(0, 0%, 15%);
    --gray-darker: hsl(0, 0%, 35%);
    --gray-dark: hsl(0, 0%, 50%);
    --gray: hsl(0, 0%, 70%);
    --gray-light: hsl(0, 0%, 80%);
    --gray-lighter: hsl(0, 0%, 90%);
    --gray-lightest: hsl(0, 0%, 97%);

    --quick-obs: #4caf50;
    --avalanche-obs: #03a9f4;
    --snowpack-obs: #3f51b5;
    --weather-obs: #ffc107;
    --incident-obs: #f44336;

    --spaw: #e6252f;
    --spaw-hover: rgb(217, 37, 46);
    --spaw-hover: rgb(217, 37, 46);

    --success: #4caf50;
    --info: #03a9f4;
    --warning: #ffc107;
    --danger: #e6252f;

    --error: #e6252f;
}

._35YU {
    position: absolute;
    top: auto;
    right: 0;
    bottom: 0;
    left: auto;
    color: hsl(0, 0%, 15%);
    color: var(--gray-darkest);
    background-color: white;
    opacity: 0.75;
    text-align: right;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    font-size: 0.75rem;
    padding: 0 0.5em;
    height: 1.5em;
    line-height: 1.5em
}

._35YU:before {
        content: 'Credit: ';
    }

._2U2Y {
    padding-right: 1.5em;
    visibility: hidden
}

._2U2Y:hover {
        visibility: visible;
    }

._2U2Y:after {
        content: 'i';
        position: absolute;
        bottom: 0;
        right: 0;
        width: 1.5em;
        height: 1.5em;
        visibility: visible;
        font-weight: bold;
        color: white;
        background-color: hsl(0, 0%, 15%);
        background-color: var(--gray-darkest);
        cursor: pointer;
        text-align: center;
        box-sizing: border-box;
    }

._2U2Y:hover:after {
        background-color: transparent;
        color: hsl(0, 0%, 15%);
        color: var(--gray-darkest);
        border-color: transparent;
    }

.g-F9 {
    visibility: visible
}

.g-F9:after {
        background-color: transparent;
        color: hsl(0, 0%, 15%);
        color: var(--gray-darkest);
        border-color: transparent;
    }

._3GcV {
    top: 0;
    bottom: auto;
}
