:root {
    --brand-primary: #245eac;
    --primary: #245eac;
    --brand-secondary: #e6252f;
    --secondary: #0f0f0f;

    --black: #0f0f0f;

    --gray-darkest: hsl(0, 0%, 15%);
    --gray-darker: hsl(0, 0%, 35%);
    --gray-dark: hsl(0, 0%, 50%);
    --gray: hsl(0, 0%, 70%);
    --gray-light: hsl(0, 0%, 80%);
    --gray-lighter: hsl(0, 0%, 90%);
    --gray-lightest: hsl(0, 0%, 97%);

    --quick-obs: #4caf50;
    --avalanche-obs: #03a9f4;
    --snowpack-obs: #3f51b5;
    --weather-obs: #ffc107;
    --incident-obs: #f44336;

    --spaw: #e6252f;
    --spaw-hover: rgb(217, 37, 46);
    --spaw-hover: rgb(217, 37, 46);

    --success: #4caf50;
    --info: #03a9f4;
    --warning: #ffc107;
    --danger: #e6252f;

    --error: #e6252f;
}

:root {
    --br-thickness: 8px;
}

._3fsa {
    display: block;
    width: 100%;
    height: 8px;
    height: var(--br-thickness);
}

._2B8N {
}

._2B8N:before {
        content: '';
        display: inline-block;
        float: left;
        background-color: #245eac;
        background-color: var(--brand-primary);
        width: 50px;
        height: 100%;
    }

._2ilA:before {
        content: '';
        position: absolute;
        bottom: 0;
        left: 0;
        right: 0;
        height: 8px;
        height: var(--br-thickness);
        background-image: repeating-linear-gradient(-45deg, transparent, transparent 1px, hsl(0, 0%, 90%) 3px, hsl(0, 0%, 90%) 4px);
        background-image: repeating-linear-gradient(-45deg, transparent, transparent 1px, var(--gray-lighter) 3px, var(--gray-lighter) 4px);
    }

._213w:after {
        content: '';
        position: absolute;
        bottom: 0;
        left: 0;
        width: 50px;
        height: 8px;
        height: var(--br-thickness);
        background-color: #245eac;
        background-color: var(--brand-primary);
    }
