.m8Jo {
    position: relative;
    color: hsl(214.41176470588235, 65.3846153846%, 50%);
    color: hsl(214.41176470588235, 65.3846153846%, 50%);
    font-size: 1.25em;
    border-bottom: hsl(0, 0%, 80%) solid 3px;
    border-bottom: var(--gray-light) solid 3px;
    margin: 25px;
    padding: 15px 0 15px 55px
}

.m8Jo:before {
        content: '\2018\2018';
        position: absolute;
        left: 0;
        top: -30px;
        color: hsl(214.41176470588235, 65.3846153846%, 35%);
        color: hsl(214.41176470588235, 65.3846153846%, 35%);
        font-size: 6em;
        font-weight: bold;
    }

.m8Jo:after {
        content: '';
        position: absolute;
        top: 0;
        right: 0;
        left: 55px;
        border-top: hsl(0, 0%, 80%) solid 3px;
        border-top: var(--gray-light) solid 3px;
    }

._2-Z1 {
    font-size: 0.6em;
    color: hsl(0, 0%, 70%);
    color: var(--gray);
    padding-top: 10px;
    text-transform: uppercase;
}
