:root {
    --brand-primary: #245eac;
    --primary: #245eac;
    --brand-secondary: #e6252f;
    --secondary: #0f0f0f;

    --black: #0f0f0f;

    --gray-darkest: hsl(0, 0%, 15%);
    --gray-darker: hsl(0, 0%, 35%);
    --gray-dark: hsl(0, 0%, 50%);
    --gray: hsl(0, 0%, 70%);
    --gray-light: hsl(0, 0%, 80%);
    --gray-lighter: hsl(0, 0%, 90%);
    --gray-lightest: hsl(0, 0%, 97%);

    --quick-obs: #4caf50;
    --avalanche-obs: #03a9f4;
    --snowpack-obs: #3f51b5;
    --weather-obs: #ffc107;
    --incident-obs: #f44336;

    --spaw: #e6252f;
    --spaw-hover: rgb(217, 37, 46);
    --spaw-hover: rgb(217, 37, 46);

    --success: #4caf50;
    --info: #03a9f4;
    --warning: #ffc107;
    --danger: #e6252f;

    --error: #e6252f;
}

.qukj {
    background-image: repeating-linear-gradient(-45deg, transparent, transparent 1px, hsl(0, 0%, 90%) 3px, hsl(0, 0%, 90%) 4px);
    background-image: repeating-linear-gradient(-45deg, transparent, transparent 1px, var(--gray-lighter) 3px, var(--gray-lighter) 4px);
}

._1lot:before {
        content: "";
        background-image: repeating-linear-gradient(-45deg, transparent, transparent 1px, hsl(0, 0%, 90%) 3px, hsl(0, 0%, 90%) 4px);
        background-image: repeating-linear-gradient(-45deg, transparent, transparent 1px, var(--gray-lighter) 3px, var(--gray-lighter) 4px);
    }

._2pHv:after {
        content: "";
        background-image: repeating-linear-gradient(-45deg, transparent, transparent 1px, hsl(0, 0%, 90%) 3px, hsl(0, 0%, 90%) 4px);
        background-image: repeating-linear-gradient(-45deg, transparent, transparent 1px, var(--gray-lighter) 3px, var(--gray-lighter) 4px);
    }
