:root {
    --brand-primary: #245eac;
    --primary: #245eac;
    --brand-secondary: #e6252f;
    --secondary: #0f0f0f;

    --black: #0f0f0f;

    --gray-darkest: hsl(0, 0%, 15%);
    --gray-darker: hsl(0, 0%, 35%);
    --gray-dark: hsl(0, 0%, 50%);
    --gray: hsl(0, 0%, 70%);
    --gray-light: hsl(0, 0%, 80%);
    --gray-lighter: hsl(0, 0%, 90%);
    --gray-lightest: hsl(0, 0%, 97%);

    --quick-obs: #4caf50;
    --avalanche-obs: #03a9f4;
    --snowpack-obs: #3f51b5;
    --weather-obs: #ffc107;
    --incident-obs: #f44336;

    --spaw: #e6252f;
    --spaw-hover: rgb(217, 37, 46);
    --spaw-hover: rgb(217, 37, 46);

    --success: #4caf50;
    --info: #03a9f4;
    --warning: #ffc107;
    --danger: #e6252f;

    --error: #e6252f;
}

.FV5H {
    display: flex;
    flex-direction: column;
    justify-content: center;

    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;

    margin: auto 2em;

    color: white;
    font-size: 1.5em;
    font-weight: 700;
    text-align: center;
    text-shadow: 0 1px 2px rgba(0, 0, 0, 0.6);
    white-space: normal;
    line-height: normal
}

.FV5H a {
        color: #e6252f;
        color: var(--brand-secondary);
    }

@media (min-width: 601px) {

.FV5H {
        margin: auto 3em;
        font-size: 2em
}
    }
